import React from 'react'
import ListPage from './ListPage'
class Index extends React.Component {
    render() {
        let options = {
            url: '/list-table.json',
            searchOnInit: true,
            conditionOptions: {
                showSearchBtn: true,
                conditionItems: [
                    [
                        {
                            type: 'input',
                            name: 'name',
                            label: '',
                            placeHolder: '',
                            searchOnChange: true
                        },
                        {
                            name: 'email',
                            label: '',
                            type: 'combobox',
                            searchOnChange: true,
                            separator: '@',
                            options: ['gmail.com', '163.com', 'qq.com']
                        }
                    ],
                    [
                        {
                            type: 'select',
                            name: 'store',
                            label: '',
                            searchOnChange: true,
                            defaultValue: 'all',
                            options: [
                                {value: 'all', text: ''},
                                {value: '1', text: ''},
                                {value: '2', text: ''},
                                {value: '3', text: ''},
                                {value: '4', text: ''}
                            ]
                        },
                        {
                            type: 'radioButton',
                            name: 'time',
                            label: '',
                            searchOnChange: true,
                            defaultValue: 'all',
                            options: [
                                {value: 'all', text: ''},
                                {value: '1', text: ''},
                                {value: '2', text: ''},
                                {value: '3', text: ''},
                                {value: '4', text: ''}
                            ]
                        }
                    ]
                ]
            },
            topToolBarOptions: {
                buttons: [
                    {
                        text: '',
                        onClick: ()=> {
                            console.log('');
                        }
                    },
                    {
                        text: '',
                        onClick: ()=> {
                            console.log('');
                        }
                    },
                    {
                        text: '',
                        onClick: ()=> {
                            console.log('');
                        }
                    }
                ]
            },
            dataTableOptions: {
                columns: [{
                    title: '',
                    dataIndex: 'name',
                    width: 150,
                }, {
                    title: '',
                    dataIndex: 'age',
                    width: 150,
                }, {
                    title: '',
                    dataIndex: 'address',
                }]
            },
            bottomToolBarOptions: {
                buttons: [
                    {
                        text: '',
                        onClick: ()=> {
                            console.log('');
                        }
                    },
                    {
                        text: '',
                        onClick: ()=> {
                            console.log('');
                        }
                    },
                    {
                        text: '',
                        onClick: ()=> {
                            console.log('');
                        }
                    }
                ]
            }
        }
        return (
            <ListPage {...options}/>
        )
    }
}
export default Index;


